import React, { useEffect, useState } from 'react'
import styles from './AddRemovePopup.module.css'


const AddRemovePopup = ({ currenttype, setweb3instance, accounts, stakingcontract, lptokeninstance, web3instance, mode, setaddremovemenu }) => {
    const [balance, setbalance] = useState(0)
    const [amountInput, setamountInput] = useState("")
    const [allowance, setAllowance] = useState(0)
    const [showAllowanceButton, setshowAllowanceButton] = useState(false)
    const [type, settype] = useState(0)
    useEffect(async () => {
        if (web3instance != null) {
            setbalance(await lptokeninstance.methods.balanceOf(accounts[0]).call())
            var allowance = await lptokeninstance.methods.allowance(accounts[0], "0x35975526f7e4ec4b8ed051509f4fe3e45dd31ae2").call();
            setAllowance(allowance)
            if (allowance == 0) {
                setshowAllowanceButton(true)
            }
        }
    }, [web3instance])

    async function confirm() {
        var number = parseFloat(amountInput) * 1000000000000000000
        await stakingcontract.methods.deposit(number, type).send({ from: accounts[0] })
        setaddremovemenu(0)
        var w3instance = web3instance //trigger web3 update
        setweb3instance(null)
        setweb3instance(w3instance)
    }

    function changeInput(e) {
        setamountInput(e.target.value)
        if (parseFloat(amountInput) <= allowance) {
            setshowAllowanceButton(false)
        } else {
            setshowAllowanceButton(true)
        }
        if (allowance == 0) {
            setshowAllowanceButton(true)
        }
    }

    async function approve() {
        await lptokeninstance.methods.approve("0x35975526f7e4ec4b8ed051509f4fe3e45dd31ae2", "0xffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff").send({ from: accounts[0] })
        var newallowance = await lptokeninstance.methods.allowance(accounts[0], "0x35975526f7e4ec4b8ed051509f4fe3e45dd31ae2").call();
        setAllowance(newallowance)
        setshowAllowanceButton(false)
    }

    if (mode != 0) {
        return (
            <div onClick={(e) => {
                if (e.target === e.currentTarget) {
                    setaddremovemenu(0)
                }
            }} class={styles.bodycontainer}>
                <div class={styles.body}>
                    <p class={styles.heading}>{mode == 1 ? "Deposit" : "Withdraw"}</p>
                    <div class={styles.numbercontainer}>
                        <p class={styles.available}>Balance: {balance / 1000000000000000000} Cake-LP</p>
                        <div class={styles.inputcontainer}>
                            <input value={amountInput} onChange={changeInput} class={styles.input} />
                            <p class={styles.cakehint}>Cake-LP</p>
                            <p onClick={() => { setamountInput(balance / 1000000000000000000) }} class={styles.maxbutton}>Max</p>
                        </div>
                    </div>
                    {currenttype != 0 && <p class={styles.warningmessage}>Depositing while already staking will reset the timer to {currenttype * 2} weeks</p>}
                    {currenttype == 0 &&
                        <div class={styles.typechooser}>
                            <div onClick={() => { settype(1) }} class={`${styles.typechoosertype} ${type == 1 ? styles.selectedtype : null}`}>2 Weeks Locked - 212.12%</div>
                            <div onClick={() => { settype(2) }} class={`${styles.typechoosertype} ${type == 2 ? styles.selectedtype : null}`}>4 Weeks Locked - 300.54%</div>
                            <div onClick={() => { settype(3) }} class={`${styles.typechoosertype} ${type == 3 ? styles.selectedtype : null}`}>6 Weeks Locked - 413.21%</div>
                        </div>
                    }
                    <div class={styles.buttonpair}>
                        <div onClick={() => { setaddremovemenu(0) }} class={`${styles.button} ${styles.cancelbutton}`}>Cancel</div>
                        {!showAllowanceButton && <div onClick={() => { confirm() }} class={styles.button}>Confirm</div>}
                        {showAllowanceButton && <div onClick={() => { approve() }} class={styles.button}>Approve</div>}
                    </div>
                </div>
            </div>
        )
    } else {
        return null;
    }
}

export default AddRemovePopup
