import { useEffect, useState } from 'react';
import styles from './App.module.css'
import AddRemovePopup from './components/AddRemovePopup';
import Web3 from "web3";
import stakingcontractabi from './abi/stakingcontractabi.json'
import IBEP20 from './abi/IBEP20.json'


function App() {
  const [addremovemenu, setaddremovemenu] = useState(0)
  const [walletConnected, setwalletConnected] = useState(false)
  const [web3instance, setweb3instance] = useState(null)
  const [stakingcontract, setstakingcontract] = useState(null)
  const [lptokeninstance, setlptokeninstance] = useState(null)
  const [accounts, setaccounts] = useState([])

  function secondsToDhms(seconds) {
    seconds = Number(seconds);
    var d = Math.floor(seconds / (3600 * 24));
    var h = Math.floor(seconds % (3600 * 24) / 3600);
    var m = Math.floor(seconds % 3600 / 60);
    var s = Math.floor(seconds % 60);

    var dDisplay = d > 0 ? d + "D " : "";
    var hDisplay = h > 0 ? h + "H " : ""
    var mDisplay = m > 0 ? m + "M " : ""
    var sDisplay = s > 0 ? s + "S " : ""
    return dDisplay + hDisplay + mDisplay + sDisplay;
  }

  const [rewardsText, setrewardsText] = useState(0)
  const [timerUpdate, settimerUpdate] = useState(0)

  const [amount, setamount] = useState(0)
  const [timelock, settimelock] = useState(0)
  const [type, settype] = useState(0)
  const [pending, setpending] = useState(0)
  async function connectWallet() {
    const web3 = new Web3();
    if (window.ethereum) {
      console.log("2s")
      web3.eth.setProvider(window.ethereum)
      await window.ethereum.enable();
      if (await web3.eth.net.getId() != 56) {
        await window.ethereum.request({
          method: 'wallet_addEthereumChain',
          params: [
            {
              chainId: `0x${(56).toString(16)}`,
              chainName: 'Binance Smart Chain Mainnet',
              nativeCurrency: {
                name: 'BNB',
                symbol: 'bnb',
                decimals: 18,
              },
              rpcUrls: ["https://bsc-dataseed.binance.org/"],
              blockExplorerUrls: ['https://bscscan.com/'],
            },
          ],
        })
      }
      setstakingcontract(new web3.eth.Contract(
        stakingcontractabi,
        "0x35975526f7e4ec4b8ed051509f4fe3e45dd31ae2"
      ))
      setlptokeninstance(new web3.eth.Contract(
        IBEP20,
        "0xf4761E11a0475Dd977D46cAa86C351f82a90234e"
      ))
      setaccounts(await web3.eth.getAccounts())
      setwalletConnected(true)
      setweb3instance(web3)
    }
  }
  useEffect(async () => {
    if (web3instance != null) {
      var userinfo = await stakingcontract.methods.userInfo(accounts[0]).call()
      console.log(userinfo)
      if (userinfo._type != 0) {
        settype(userinfo._type)
        setamount(userinfo.amount)
        settimelock(userinfo.timeLock)
        setpending((await stakingcontract.methods.getPendingAtEndOfStakingPeriod().call({ from: accounts[0] }))[0])
        console.log((Date.now() / 1000) > timelock)
      }
    }
    setInterval(() => {
      settimerUpdate(timerUpdate + 1)
    }, 500)
  }, [web3instance])

  useEffect(() => {
    _update()
  }, [timerUpdate])

  function _update() {
    var timestarted = timelock - ((1209600) * type)
    console.log("timestarted: " + timestarted)
    var currenttime = Date.now()/1000
    console.log("currenttime: " + currenttime)
    console.log("timelock: " + timelock)
    var percentage = (currenttime - timestarted) / (timelock - timestarted)
    console.log(percentage)
    if(currenttime > timelock) {
      percentage = 1
    }
    setrewardsText(((pending * percentage) / 1000000000).toFixed(0))
  }

  return (
    <div class={styles.contentcontainer}>
      <AddRemovePopup setweb3instance={setweb3instance} currenttype={type} stakingcontract={stakingcontract} accounts={accounts} lptokeninstance={lptokeninstance} web3instance={web3instance} setaddremovemenu={setaddremovemenu} mode={addremovemenu} />
      <div class={styles.topbar}>
        <div class={styles.topbarleftalign}>
          <div class={styles.logo} />
          <p class={styles.logostaking}>STAKING EVENT</p>
        </div>
        {walletConnected ? <div class={`${styles.connectwalletbutton} ${styles.connectwalletbuttonactive}`}>Connected</div> :
          <div onClick={connectWallet} class={styles.connectwalletbutton}>Connect Wallet</div>}
      </div>
      <p class={styles.infotext}>
        The staking event is active until block <a class={styles.infotextlink} target="_blank" href="https://bscscan.com/block/123456789">#123456789</a><br />
        For staking, you need to supply BLUEFLOKI and BNB on the<br />
        PancakeSwap liquidity pool.<br />
        <a class={styles.infotextlink} target="_blank" href="https://docs.pancakeswap.finance/products/pancakeswap-exchange/liquidity-guide">Here</a> is a tutorial.
      </p>
      <div class={styles.stakecard}>
        <p class={styles.stakecardheading}>BLUEFLOKI-BNB LP</p>
        <div class={styles.lpicon} />
        <div class={styles.inforow}>
          <p>APR:</p>
          {type == 0 && <p>up to 413.21%</p>}
          {type == 1 && <p>212.12%</p>}
          {type == 2 && <p>300.54%</p>}
          {type == 3 && <p>413.21%</p>}
        </div>
        <div class={styles.inforow}>
          <p>Earn:</p>
          <p>BLUEFLOKI</p>
        </div>
        <div class={styles.inforow}>
          <p>Deposit fee:</p>
          <p>0%</p>
        </div>
        <div class={styles.inforow}>
          <p>Withdraw fee:</p>
          <p>0%</p>
        </div>
        {type != 0 &&
          <>
            <div class={`${styles.inforow} ${styles.extramargin}`}>
              <div>
                <p>Rewards</p>
                <p class={styles.infoundertext}>{rewardsText} BLUEFLOKI</p>
              </div>
              {timelock < (Date.now() / 1000) && <div class={styles.harvestbutton}>Harvest</div>}
              {timelock > (Date.now() / 1000) && <div style={{ backgroundColor: '#636363' }} class={styles.harvestbutton}>Harvest</div>}

            </div>
            <div class={styles.inforow}>
              <p>Timer</p>
              <p>{secondsToDhms(timelock - (Date.now() / 1000))}</p>
            </div>
          </>
        }
        {web3instance != null &&
          <div class={`${styles.inforow} ${styles.extramargin}`}>
            <div>
              <p>Cake-LP staked:</p>
              <p class={styles.infoundertext}>{amount / 1000000000000000000}</p>
            </div>
            <div class={styles.addremovebuttoncontainer}>
              <div onClick={() => { setaddremovemenu(1) }} class={styles.addremovebutton}>+</div>
            </div>
          </div>
        }
      </div>
    </div>
  );
}

export default App;
